 .wrapper {
  width: 80%;
  margin: 15px;
  border: 1px solid black;
  display: flex;
  flex-direction: column;
  align-items:center;
  h1 {
    margin: 1.8rem 0;
  }
  @media(max-width: 950px) {
    width: 100%;
    margin: 0 auto;
  }
   ol {
     display: flex;
     justify-content: center;
     flex-wrap: wrap;
     font-size: 1.5rem;
     margin: 0 1rem;
     padding: 0;
     @media(max-width: 950px){
       flex-direction: column;
     }
   }
   ol > li {
    margin: .2rem 1.4rem;
    color: red;
    span {
      color: black;
    }
   }
}